// required for 3-dot pulse animation
.animate-delay-100 {
  animation-delay: 0s !important;
}

.animate-delay-200 {
  animation-delay: 0.4s !important;
}

.animate-delay-300 {
  animation-delay: 0.8s !important;
}

@keyframes pulseFlow {
  0%, 100% {
    transform: scale(0.9);
    opacity: 0.4;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-pulse-flow {
  animation: pulseFlow 2s ease-in-out infinite;
}
